
.modalSmoll{
  transition-duration: 0.3s;
  width:100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
  display: flex;
  align-items: left ;
  justify-content:left;
  transform: translateX(-100%);
  
  }
  .modalSmoll.ActiveSmoll{
    transition-duration: 0.2s;
    transform: translateX(0px);
  }

.ModalSmollContent{
    width: auto;
    padding: 20px;
    text-align: left;
    background-color: #f2ebe3;
    text-align: center;
    color: black;
    border-right: 3px solid rgba(177, 85, 9, 0.76)
    
   
}.MenuSmollIcon{
    width: 100%;
    display: flex;
    justify-content: center;
}
.countSmoll{
    position: relative;
    bottom: 15px;
    color: rgb(184, 8, 8);
    font-size: 20px;
}
.SmollImage{
    width: 165px;
    border: 1px solid none;
    border-radius: 50%;
    padding: 10px;
    /* background:linear-gradient(0.25turn, rgba(212, 8, 8, 0.911),rgb(221, 164, 6)) ; */
   display: flex;
   justify-content: center;
}


.MenuSmollIcon img{
    width: 120px;
    height: 140px;
}
.smoll{

    text-align: left;
    display: grid;
    list-style-type: none;
 
   
}
.smoll li {
    cursor: pointer;
    text-align: left;
    font-size: 25px;
    padding: 1vh;
    color: #673f35;
    
    
}
a{  
    text-decoration: none;
    color: #673f35;
}
.smoll li
    a:hover{
    color: #ce3a31;
}
/* @media screen and (min-width: 1100px){
.modalSmoll{
    display: none;
}
} */

@media (max-width: 350px) {
    .ModalSmollContent{
        width: 70%;
    }
    .SmollImage img{
        width: 100px;
        height: 115px;
    }
    .smoll li{
        font-size: 23px;
    }
}